.grid {
  margin: 0 auto;
  max-width: 1290px;
  overflow: hidden;

  * {
    box-sizing: border-box;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.col {
  padding-left: 15px;
  padding-right: 15px;
  width: 100%;
}

.col-1 {
  width: 8.333333333%;
}

.col-2 {
  width: 16.66666667%;
}

.col-3 {
  width: 25%;
}

.col-4 {
  width: 33.33333333%;
}

.col-5 {
  width: 41.66666667%;
}

.col-6 {
  width: 50%;
}

.col-7 {
  width: 58.33333333%;
}

.col-8 {
  width: 66.66666667%;
}

.col-9 {
  width: 75%;
}

.col-10 {
  width: 83.33333333%;
}

.col-11 {
  width: 91.66666667%;
}

.col-12 {
  width: 100%;
}

.offset-col-1 {
  margin-left: 8.333333333%;
}

.offset-col-2 {
  margin-left: 16.66666667%;
}

.offset-col-3 {
  margin-left: 25%;
}

/*
medium—upp að 800px
*/
@media (max-width: 799px) {
  .col-md-1 {
